import React, { useEffect, useState } from 'react';
import avatar from '../../assets/imgs/user.png';
import Img from '../../components/Img';
import {
  BriefcaseIcon,
  CogIcon,
  HeartIcon,
  LiquidIcon,
  ModelIcon,
  NumberIcon,
  StarIcon,
  StarSolidIcon,
  UsersIcon,
} from '../../components/Svg';
import Button from '../../components/Button';

import './styles/CarInfos.scss';
import http from '../../utils/http';
import { useUserContext } from '../../contexts/User';
import { toast } from 'react-toastify';
import Comment from '../../components/Comment/Comment';

const CarInfos = ({ car, comments, setIsRevalidate }) => {
  const [error, setError] = useState(null);
  const { user } = useUserContext();
  const [localLiked, setLocalLiked] = useState(false);

  const addFavorite = async () => {
    if (car?.customer_id === user?.customer_id)
      return 'Bn khng th yu thch xe ca chnh mnh!';
    if (!!car?.favorite_car_id) {
      try {
        const { data } = await http.delete(
          `/delete_favorite_car?favorite_car_id=${car?.favorite_car_id}`
        );
        if (data.status === 1) {
          toast(' b khi mc Xe a thch');
          setLocalLiked((prev) => !prev);
          setIsRevalidate((prev) => !prev);
        }
      } catch (error) {
        setError('Khng thnh cng, th li sau!');
      }
    } else {
      try {
        const { data } = await http.post('/add_favorite_car', {
          car_id: car?.car_id,
          customer_id: user?.id || localStorage.getItem('USER_ID'),
        });
        if (data.status === 1) {
          toast(' thm vo mc Xe a thch');
          setLocalLiked((prev) => !prev);
          setIsRevalidate((prev) => !prev);
        }
      } catch (error) {
        setError('Khng thnh cng, th li sau!');
      }
    }
  };

  useEffect(() => {
    console.log('???');
    setLocalLiked(!!car?.favorite_car_id);
  }, [car?.favorite_car_id]);

  return (
    <div className="CarInfos">
      <div className="infos">
        <div className="base-infos">
          <div className="name">{car?.model_name}</div>
          <div className="car-info">
            <div className="block-info">
              <StarSolidIcon className="star-icon icon" />
              <span>{car?.rate ? Number(car?.rate).toFixed(1) : '5.0'}</span>
            </div>
            <div className="block-info">
              <BriefcaseIcon className="icon" />
              <span>{car?.count_journeys} chuyn</span>
            </div>
          </div>
        </div>
        <Button
          disabled={!user}
          variant="outline"
          className={`like-btn ${localLiked ? 'liked' : ''}`}
          onClick={addFavorite}
        >
          <HeartIcon />
        </Button>
      </div>
      <div className="main-infos">
        <div className="main-info">
          <UsersIcon />
          <div className="t-value">
            <div className="title">S gh</div>
            <div className="value">{car?.number_seats} ch</div>
          </div>
        </div>
        <div className="main-info">
          <NumberIcon />
          <div className="t-value">
            <div className="title">Bin s</div>
            <div className="value">{car?.number_plate}</div>
          </div>
        </div>
        <div className="main-info">
          <CogIcon />
          <div className="t-value">
            <div className="title">i xe</div>
            <div className="value">2018</div>
          </div>
        </div>
        <div className="main-info">
          <ModelIcon />
          <div className="t-value">
            <div className="title">Hng xe</div>
            <div className="value">{car?.brand_name}</div>
          </div>
        </div>
        {/* <div className="main-info">
          <LiquidIcon />
          <div className="t-value">
            <div className="title">Nhin liu</div>
            <div className="value">Xng</div>
          </div>
        </div> */}
      </div>
      <div style={{ color: '#767676', fontSize: 15 }} className="description">
        <div className="title">M t</div>
        <div className="value">{car?.description}</div>
      </div>
      <pre
        style={{
          color: '#767676',
          fontSize: 15,
          overflow: 'hidden',
          textOverflow: 'ellipsis',
          width: '700px',
        }}
        className="mt-5"
      >
        Quy nh khc:{'\n'} S dng xe ng mc ch.{'\n'} Khng s dng xe
        thu vo mc ch phi php, tri php lut.{'\n'} Khng s dng xe thu
         cm c, th chp.{'\n'} Khng ht thuc, nh ko cao su, x rc
        trong xe.{'\n'} Khng ch hng quc cm d chy n.{'\n'} Khng ch
        hoa qu, thc phm nng mi trong xe.{'\n'} Khi tr xe, nu xe bn hoc
        c mi trong xe, khch hng vui lng v sinh xe sch s hoc gi ph thu
        ph v sinh xe.{'\n'}Trn trng cm n, chc qu khch hng c nhng
        chuyn i tuyt vi !{'\n'}
        {'                '}
      </pre>
      <div className="mt-5"></div>
      <div className="on_xe">Ch xe</div>
      <div className=" d-flex align-items-center mt-2">
        <img src={avatar} alt="avatar" height="80px" width="80px" />
        <div className="info3">
          <div className="name ms-3">{user?.name_display}</div>
        </div>
      </div>
      <div className="mt-3"></div>
      {comments.length > 0 && (
        <div className="comments mt-4">
          <div className="comment_grap">
            <StarSolidIcon className="" width="16" height="16" fill="#f0c541" />
            <div className="ms-1">
              {car?.rate_avg ? Number(car?.rate_avg).toFixed(1) : '--'}{' '}
            </div>
            <span className="ms-1"></span>{' '}
            <div className="ms-1 dg_f">
              {car?.count_comment || '--'} nh gi
            </div>
          </div>

          <div className="rows mt-3">
            {comments.map((cmt) => (
              <Comment
                comment={cmt.comment}
                create_at={cmt.create_at}
                create_by={cmt.create_by}
                rate={cmt.rate}
                name={cmt?.name_display}
              />
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default CarInfos;
