import React, { useEffect, useState } from 'react';

import Filter from './components/Filter';
import Cars from './components/Cars';

import './styles/Home.scss';
import { useNavigate, useSearchParams } from 'react-router-dom';
import useModal from '../hooks/useModal';
import Register from '../components/modals/Register';
import Login from '../components/modals/Login';
import Loader from '../components/Loader/Loader';
import useScrollToTop from '../hooks/useScrollToTop';

const Home = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const [showRegister] = useModal(<Register />);
  const [showLogin] = useModal(<Login switchToRegister={showRegister} />);
  const navigate = useNavigate();
  useScrollToTop();
  useEffect(() => {
    const isReLogin = JSON.parse(searchParams.get('re-login'));
    const isReLoad = JSON.parse(searchParams.get('re-load'));
    if (isReLogin) {
      localStorage.clear();
      showLogin();
      searchParams.delete('re-login');
      navigate('/', { replace: true });
    }
    if (isReLoad) {
      searchParams.delete('re-load');
      navigate('/', { replace: true });
      window.location.reload();
    }
  }, [navigate, searchParams, setSearchParams, showLogin]);

  const [filter, setFilter] = useState({});
  return (
    <div className="Home page-layout large-margin flash">
      <Filter filter={filter} setFilter={setFilter} />
      <Cars filter={filter} />
      <div className="car_pro_name_sen"></div>
      <section>
        <div className="mt-5">
          <div className="row">
            <div className="col-lg-12 mb-4 mt-2 text-center">
              <h2>u im Ca Mioto</h2>
              <h4>
                Nhng tnh nng gip bn d dng hn khi thu xe trn Mioto.
              </h4>
            </div>
          </div>
        </div>
        <div className="container ">
          <div className="row">
            <div className="col-lg-4 col-md-6 col-sm-12 text-center mt-3">
              <img
                className="rounded-circle"
                alt="140x140"
                style={{ width: 240, height: 200 }}
                src="https://localhost:44307/Image/Home/img1.png"
                data-holder-rendered="true"
              />
              <h4>An tm t xe</h4>
              <p style={{ fontSize: 13 }}>
                Khng tnh ph hu chuyn trong vng 1h sau khi t cc. Hon
                cc v bi thng 100% nu ch xe hu chuyn trong vng 7 ngy
                trc chuyn i.
              </p>
            </div>
            <div className="col-lg-4 col-md-6 col-sm-12 text-center mt-3">
              <img
                className="rounded-circle"
                alt="140x140"
                style={{ width: 240, height: 200 }}
                src="https://localhost:44307/Image/Home/img2.png"
                data-holder-rendered="true"
              />
              <h3>Th tc n gin</h3>
              <p style={{ fontSize: 13 }}>
                Ch cn c CCCD gn chip (Hoc Passport) &amp; Giy php li xe
                l bn   iu kin thu xe trn Mioto.
              </p>
            </div>
            <div className="col-lg-4 col-md-6 col-sm-12 text-center mt-3">
              <img
                className="rounded-circle"
                alt="140x140"
                style={{ width: 240, height: 200 }}
                src="https://localhost:44307/Image/Home/img6.png"
                data-holder-rendered="true"
              />
              <h3>Li xe an ton</h3>
              <p style={{ fontSize: 13 }}>
                Hn 100 dng xe cho bn tu  la chn: Mini, Sedan, CUV, SUV,
                MPV, Bn ti.
              </p>
            </div>
            <div className="col-lg-4 col-md-6 col-sm-12 text-center mt-3">
              <img
                className="rounded-circle"
                alt="140x140"
                style={{ width: 240, height: 200 }}
                src="https://localhost:44307/Image/Home/img3.png"
                data-holder-rendered="true"
              />
              <h3>Thanh ton d dng</h3>
              <p style={{ fontSize: 13 }}>
                a dng hnh thc thanh ton: ATM, th Visa &amp; V in t
                (Momo, VnPay, ZaloPay).
              </p>
            </div>
            <div className="col-lg-4 col-md-6 col-sm-12 text-center mt-3">
              <img
                className="rounded-circle"
                alt="140x140"
                style={{ width: 240, height: 200 }}
                src="https://localhost:44307/Image/Home/img4.png"
                data-holder-rendered="true"
              />
              <h3>Giao xe tn ni</h3>
              <p style={{ fontSize: 13 }}>
                Bn c th la chn giao xe tn nh/sn bay... Ph tit kim ch
                t 15k/km.
              </p>
            </div>
            <div className="col-lg-4 col-md-6 col-sm-12 text-center mt-3">
              <img
                className="rounded-circle"
                alt="140x140"
                style={{ width: 240, height: 200 }}
                src="https://localhost:44307/Image/Home/img5.png"
                data-holder-rendered="true"
              />
              <h3>Dng xe a dng</h3>
              <p style={{ fontSize: 13 }}>
                Hn 100 dng xe cho bn tu  la chn: Mini, Sedan, CUV, SUV,
                MPV, Bn ti.
              </p>
            </div>
          </div>
        </div>
        <div className="mt-5" />
        <div className="col-lg-12 mb-4 mt-2 text-center">
          <h3>Hng Dn Thu Xe</h3>
          <h5>
            Ch vi 4 bc n gin  tri nghim thu xe Mioto mt cch nhanh
            chng.
          </h5>
        </div>
        <div className="row">
          <div className="col-lg-3 col-md-5 col-sm-11 text-center">
            <img
              className="rounded-circle"
              alt="140x140"
              style={{ width: 240, height: 200 }}
              src="https://localhost:44307/Image/Home/img7.png"
              data-holder-rendered="true"
            />
            <h5 style={{ fontSize: 14 }}>t xe trn web ADDDA</h5>
          </div>
          <div className="col-lg-3 col-md-5 col-sm-11 text-center">
            <img
              className="rounded-circle"
              alt="140x140"
              style={{ width: 240, height: 200 }}
              src="https://localhost:44307/Image/Home/img8.png"
              data-holder-rendered="true"
            />
            <h5 style={{ fontSize: 14 }}>Nhn xe</h5>
          </div>
          <div className="col-lg-3 col-md-5 col-sm-11 text-center">
            <img
              className="rounded-circle"
              alt="140x140"
              style={{ width: 240, height: 200 }}
              src="https://localhost:44307/Image/Home/img9.png"
              data-holder-rendered="true"
            />
            <h5 style={{ fontSize: 14 }}>Bt u hnh trnh</h5>
          </div>
          <div className="col-lg-3 col-md-5 col-sm-11 text-center">
            <img
              className="rounded-circle"
              alt="140x140"
              style={{ width: 240, height: 200 }}
              src="https://localhost:44307/Image/Home/img10.png"
              data-holder-rendered="true"
            />
            <h5 style={{ fontSize: 14 }}>Tr xe &amp; kt thc chuyn i</h5>
          </div>
          <div style={{ clear: 'both' }} />
        </div>
      </section>
    </div>
  );
};

export default Home;
